/* Global Styles Start Here */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
    font-size: 62.5%;
}

:root {
    /* font sizes */
    --fs-h1: 4rem;
    --fs-h2: 3rem;
    --fs-h3: 2.5rem;
    --fs-h4: 2rem;
    --fs-h5: 1.5rem;
    --fs-p: 1rem;
    /* colors */
    --clr-white: #ffffff;
    --clr-black: #000000;
}

body {
    height: auto;
    min-height: 100vh;
    width: 100%;
    max-width: 100vw;
}

.btn-home, .btn-categories {
    position: relative;
    height: 50px;
    width: 150px;
    color: #000;
    background-color: lightgray;
    border: 1px solid #000;
    outline: none;
}
/* Global Styles End Here */

