/* Main Styles Start Here */
.main {
    position: relative;
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    justify-content: space-evenly;
    align-items: center;
}

.headline {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 65%;
    padding: 1rem;
    align-self: start;
    margin-top: 3rem;
}
.headline-title {
    font-size: 2rem;
}
.headline-img {
    position: relative;
    display: flex;
}
.headline-img img {
    width: 100%;
}
.headline-text {
    font-size: 2rem;
    text-align: left;
    width: 100%;
    padding: 0.5rem;
}
.headline-text p {
    margin-bottom: 1.5rem;
    line-height: 32px;
}
.headline-text p span {
    color: red;
    text-decoration: underline;
}
/* Main Styles end Here */

/* Sidebar Styles Start Here */
.sidebar {
    position: relative;
    width: 30%;
    margin-top: 4rem;
}

.sidebar ul li {
    list-style: none;
}

.sidebar-post {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: left;
}
.sidebar-title h3 {
    font-size: 2rem;
}

.sidebar-image img {
    width: 385px;
}
.sidebar-text {
    margin-bottom: 4rem;
    font-size: 1.5rem;
}
.sidebar-text p span {
    color: red;
    text-decoration: underline;
}
/* Sidebar Styles End Here */

/* Lower News Posts Styles Start Here */
.news-posts {
    width: 100%;
    max-width: 100vw;
}

.news-posts-area {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 100%;
    margin-bottom: 2rem;
}

.news-posts-area ul {
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.news-posts-area ul li {
    list-style: none;
    display: flex;
    flex-direction: row;
    width: 30%;
}

.news-post {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    text-align: left;
    padding: 1rem;
}

.news-post-title h3 {
    font-size: 1.6rem;
    margin-top: 1rem;
}

.news-post-content {
    position: relative;
    display: flex;
    flex-direction: row;
    margin-top: 1rem;
}
.news-post-image img {
    width: 150px;
    padding-top: 1rem;
    margin-right: 1rem;
}
.news-post-text {
    text-align: left;
    padding: .5rem;
    font-size: 1.4rem;
}
.news-post-text p span {
    color: red;
    text-decoration: underline;
}
/* Lower News Posts Styles End Here */

