/* Footer Styles Start Here */
.footer-links {
    background: linear-gradient(90deg, rgb(110, 94, 254) 0%, rgba(73, 63, 252, 1) 100%);
}
.footer-links ul {
    position: relative;
    display: flex;
    list-style: none;
    justify-content: center;
    align-items: center;
    height: 60px;
}
.footer-links ul a {
    text-decoration: none;
}
.footer-links ul li {
    margin-left: 1rem;
    margin-right: 1rem;
    text-transform: uppercase;
    padding: 0.5rem 1rem;
    font-size: 1.5rem;
    letter-spacing: 2px;
    color: #fff;
}
/* Footer Styles End Here */

/* About Page Styles Start Here */
.about {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 78.75vh;
    justify-content: center;
    align-items: center;
}
.about-info h1 {
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    margin: 1rem 0;
}
.about-info p {
    position: relative;
    display: flex;
    justify-content: center;
    padding: 1rem 5rem;
    font-size: 1.5rem;
    letter-spacing: 1.5px;
}
.about-btn {
    margin: 2rem 0;
}
/* About Page Styles End Here */

/* Contact Page Styles Start Here */
.contact {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 78.75vh;
    justify-content: center;
    align-items: center;
}
.contact-info h1 {
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    margin: 1rem 0;
}
.contact-info p {
    position: relative;
    display: flex;
    padding: 1rem 5rem;
    font-size: 1.5rem;
    letter-spacing: 1.5px;
    text-align: left;
}
.contact-btn {
    margin: 2rem 0;
}
/* Contact Page Styles End Here */

/* Page Not Found Styles Start Here */
.page-not-found {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    min-height: 78.75vh;
    width: 100%;
}
.page-not-found h1 {
    margin-bottom: 2rem;
}
/* Page Not Found Styles End Here */

